.left{
  z-index: 100;
  //width: 80%;

  position: fixed;
  left: 0;
  top: 0;
  transition: left .3s ease 0s;


  display: flex;






  .kategory{
    overflow-y: auto;
    height: 750px;
    display: flex;
    background-color: white;
    .scrol{
      height: 670px;
      overflow-y: auto;
    }


    .name{


      .name-tabler {

        .name-table{
          color: black;
        }
        &:hover {

          .name-table {
            background: #cb11ab;
            color: white;
          }

          .right-part {
            display: block;

          }
        }
      }


      display: flex;
      .name-table{
        margin:0 30px 0 30px;
        padding: 5px;
        border-radius: 10px;
        width: 200px;
        cursor: pointer;

        h4{
          font-size: 16px;
          line-height: 22px;
          margin-top: 3px;

        }
        .user{
          width: 30px !important;
          height: 30px !important;
        }
      }

      .right-part{
        //width: calc(100% - 308px);
        //height: 100%;

        width: 72%;
        top: 3%;
        left: 26%;

        position: absolute;
        display: none;
        .inside{
          display: flex;
          justify-content: space-between;
          h6{
            margin-top: 13px;
            color: #888888;
          }
          .foto{
            width: 40%;

            .foot-in{
              overflow: hidden;
              position: relative;
              border-radius: 12px;
              img{
                width: 100%;
                height: 100%;
                transition: all .3s ease 0s;
              }
              &:hover,&:active
              {
                transform: scale(1.02);
                will-change: transform;
                filter: brightness(80%);
              }
            }
          }
        }
      }

      //&:hover{
      //  .name-table{
      //    background: #cb11ab;
      //    color: white;
      //  }
      //  .right-part{
      //    display: block;
      //  }
      //
      //}
    }
  }
  .close{
    position: absolute;
    z-index: 300;
    right: -20%;
    width: 50px;
    top: 6%;
    height: 50px;
    border-radius: 50%;
    text-align: center;
   padding-top: 5px;
    cursor: pointer;
    font-size: 50px;
    line-height: 38px;
    color: #efefef;
  }

}


