.yandex_map_main_page{
  background-color: #e4e4e4 !important;
  width: 324px;
  height: 210px;
  margin: 0px;
  padding: 0px;
  overflow: hidden;
  // border-radius: 10px;
  
  .yandex_map_main_page_label {
    cursor: auto;
    opacity:1 !important;
    color: #fff;
    transition: transform 300ms ease-out;
    display: inline-block;
    min-height: 100%;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
    text-align: center;
    :before, :after{
      display: block !important;
      z-index: 1000;
      background-color: #003d72 !important;
      position: absolute;
      padding: 9px 12px;
      font-size: 1rem;
      height: 11px;
      width: 5px;
      line-height: 5px;
      top: 50%;
      transform: translate3d(0, -50%, 0);
      border-radius: 50%;
      cursor: pointer;   
      border: 1px solid rgba(0, 0, 0, 0.08) !important;
      color: rgb(34, 34, 34) !important;
      cursor: pointer !important;
      touch-action: manipulation !important;
    }
      
  
    .yandex_map_main_page_label::before {
      content: "\203A";
      right: 100%;
      margin-right: 20px;
      font-weight: 900;
    }
    .yandex_map_main_page_label::after {
        content: "\2039";
        left: 100%;
        margin-left: 20px;
        font-weight: 900;
    }
    img{
      height: 100%;
      width: auto;
  }
}
  .yandex_map_main_page_form {
    width:100%;
    height:100%;
    white-space: nowrap;
    :hover  .yandex_map_main_page_label::after{
      display: block;
     }
    :hover .yandex_map_main_page_label::before{
        display: block;
    }

 .yandex_map_main_page_input {
    position: absolute;
    display: none !important;       
    :focus ~ .keys {
      opacity: 0.8;
    }
  :nth-of-type(1):checked ~ .yandex_map_main_page_label:nth-of-type(1), 
  :nth-of-type(2):checked ~ .yandex_map_main_page_label:nth-of-type(2),
  :nth-of-type(3):checked ~ .yandex_map_main_page_label:nth-of-type(3),
  :nth-of-type(4):checked ~ .yandex_map_main_page_label:nth-of-type(4){
           z-index: 0;
        }
  :nth-of-type(1):checked ~ .yandex_map_main_page_label {
    transform: translate3d(0, 0, 0);
  }
  :nth-of-type(2):checked ~ .yandex_map_main_page_label {
    transform: translate3d(-100%, 0, 0);
  }
  :nth-of-type(3):checked ~ .yandex_map_main_page_label {
    transform: translate3d(-200%, 0, 0);
  }
  :nth-of-type(4):checked ~ .yandex_map_main_page_label {
    transform: translate3d(-300%, 0, 0);
  }
  .keys {
      position: fixed;
      z-index: 10;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 1rem;
      color: rgb(255, 0, 0);
      text-align: center;
      transition: all 300ms linear;
      opacity: 0;
  }
  }


                   
}
